/* Used to hide people in illustrations outside of workhours (weekdays between 8-16)*/

const isWeekend =  date => [6,0].includes(date.getDay()); // 6 = Saturday, 0 = Sunday
const isWorkHours = time => time >= 8 && time < 16;

export default function isOutsideWorkHours(){
   const now = new Date();
   return isWeekend(now) || !isWorkHours(now.getUTCHours() + 2);
}